/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Cut The Ropes","date":"03 Oct 2002","slug":"/2002-10-03-cut-the-ropes/","tags":"Y2002"},"html":"<p>I've been been smoothing out the transition from MT to b2, cleaning up the database, making the permalinks individual entries rather than anchors in an archive, replacing the MT archives with redirects to b2, adding a referer check, putting a notice in the old RSS feed that the feed has now moved, and such things. I do need to bring in the blogroll yet and massage the nav column a lot! Or maybe 2 nav columns? And I need to apply Mark Pilgrim's <a href=\"http://diveintoaccessibility.org/\">accessibility tips</a> (which I never did in MT, since I knew the next default templates would include them, which is why I stuck with the default MT template).</p>"},"next":{"fields":{"title":"YB2?","date":"02 Oct 2002","slug":"/2002-10-02-yb2/"}},"previous":{"fields":{"title":"Indian Thaw","date":"04 Oct 2002","slug":"/2002-10-04-indian-thaw/"}}},{"node":{"fields":{"title":"YB2?","date":"02 Oct 2002","slug":"/2002-10-02-yb2/","tags":"Y2002"},"html":"<p>Why did I switch blogging systems? It's directly related to the shaky situation at work. If I'm going to be looking for work in a few weeks, I need to focus on my strengths, which are PHP and MySQL. And it only makes sense to run my weblog with those same technologies. Nothing against Movable Type. It's a very good, well thought out system. But right now I need something simple that I can extend, to hone my skills, and maybe shore up my resumé.</p>\n<p>What caused the penny to drop for me was a <a href=\"http://weblog.delacour.net/archives/000716.html\">comment</a> by Jonathon Delacour that he didn't want to become a PHP programmer, his focus was on writing. Since I <em>do</em> program PHP for a living, and would like to continue to do so, I had better start living, thinking and <em>dreaming</em> in PHP. Even if I don't end up leaving my employer.</p>\n<p>I've learned that simple database structures can be a lifesaver. Movable Type uses 17 tables, and many of the fields are obsolete. pMachine, which I nearly switched to, uses 25 tables. b2 (or Cafepress, I'm not sure why there are two names) has just 5: posts, categories, comments, users, and settings. In moving my posts and comments from MT, it was fairly easy to find which columns to copy over, keeping in mind which functions in MT are not supported in b2. I had to run the text through the nl2br function in PHP, that was the only real complication.</p>\n<p>B2 does not implement a template system. The template index.php is in PHP, and you get real grease you get on your fingers when you touch it. I like that. I'm looking forward to getting my fingers dirty.</p>"},"next":{"fields":{"title":"Our Visit To The Consulate","date":"02 Oct 2002","slug":"/2002-10-02-our-visit-to-the-consulate/"}},"previous":{"fields":{"title":"Cut The Ropes","date":"03 Oct 2002","slug":"/2002-10-03-cut-the-ropes/"}}},{"node":{"fields":{"title":"Our Visit To The Consulate","date":"02 Oct 2002","slug":"/2002-10-02-our-visit-to-the-consulate/","tags":"Y2002"},"html":"<p>Today was the big day. I had the day off alone with Christopher (both the <i>Tagesmutter</i> and her backup are on vacation this week, so Mama, Oma and I are juggling shifts), I had all the papers, so we were off to the US Consulate, the 'Little White House' on the shore of the <i>Aussen Alster</i> lake in Hamburg, to apply for Christopher's citizenship.</p>\n<p>Security was immense. The street and sidewalk in front of the building are closed for an entire city block, with no less than 4 police containers and at least 8 German officers armed with automatic weapons. They checked my passport before entering the street, and escorted  us to the gate, weapon in hand. There we were handed over to the Americans. My passport was checked again, my cell phone was taken away, and we were sent through the metal detector. We could then enter the passport office.</p>\n<p>I hadn't planned to apply for the passport today, since I thought the 'Report of Birth Abroad' had to be processed first, so I had no pictures ready. But I was advised to apply anyway, since I needed to take an oral oath (raise my right hand, the whole bit) in front of a State Dept officer for both applications, and could thus save a trip by applying for both today. We also need Mama's written consent (I guess to prevent child-napping), but we can submit that along with the pictures by mail. Good thing I stopped at the bank. Cost: $65 for the Report of Birth, $30 for the passport with a $40 surcharge for a child, for a grand total of $135 (Euros accepted at 1:1). And please include a A4 <a href=\"http://www.genealogy.com/00000820.html\">SASE</a> with the pictures.</p>\n<p>Christopher was more impressed by the sailboats on the <i>Alster</i>.</p>"},"next":{"fields":{"title":"Something New","date":"01 Oct 2002","slug":"/2002-10-01-something-new/"}},"previous":{"fields":{"title":"YB2?","date":"02 Oct 2002","slug":"/2002-10-02-yb2/"}}}],"pathPrefix":"","first":false,"last":false,"index":837,"pageCount":1101}}

/***/ })

